    padding-top: 50px;
    font-family: 'Times New Roman'
  }

  #titleviewallfeedback{
    text-align: center;
    font-family: 'Times New Roman'
  }
  
  #viewAllFeedbackTable {
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    border: 3px solid #ddd;
    width: 100%;
    counter-reset: 0;
   
  }
  #viewAllFeedbackTable tr {
    counter-increment: rowNumber;
}

#viewAllFeedbackTable tr td:first-child::before {
    content: counter(rowNumber);
    min-width: 1em;
    margin-right: 0.5em;
}
  
  #viewAllFeedbackTable td, #table th {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  #viewAllFeedbackTable tr:nth-child(even){background-color: #f2f2f2;}

  
  #viewAllFeedbackTable tr:hover {background-color: #ddd;}
  
  #viewAllFeedbackTable th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: center;
    background-color: #2A3F54;
    color: white;
  }
  .fa.fa-edit.fa-2x{
    color: #2A3F54;

  }
  .fa.fa-trash.fa-2x{
    color: #2A3F54;

  }

  .divHeight{
    height: 150px !important;
    width : 200px !important;
  }