.pendingInterviewApproval{
    font-size: 15;
    margin-top: 2%;
}
.fa-file{
color: #000000;
}

.popupSelect{
    width: 58%;
}

#title {
    text-align: center;
    font-family: 'Times New Roman', Times, serif;
  }
  
  #pendingInterviewApproval {
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    border: 3px solid #ddd;
    width: 100%;
  }
  
  #pendingInterviewApproval td, #table th {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  #pendingInterviewApproval tr:nth-child(even){background-color: #f2f2f2;}

  
  #pendingInterviewApproval tr:hover {background-color: #ddd;}
  
  #pendingInterviewApproval th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: center;
    background-color: #2A3F54;
    color: white;
  }
