#title {
    text-align: center;
    font-family: 'Times New Roman', Times, serif;
  }

  #titlescheduledinterview {
    text-align: center;
    font-family: 'Times New Roman';
  }
  
  #ScheduledInterviewTable {
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    border: 3px solid #ddd;
    width: 100%;
  }
  
  #ScheduledInterviewTable td, #table th {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  #ScheduledInterviewTable tr:nth-child(even){background-color: #f2f2f2;}

  
  #ScheduledInterviewTable tr:hover {background-color: #ddd;}
  
  #ScheduledInterviewTable th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: center;
    background-color: #2A3F54;
    color: white;
  }
