#titleAllUser {
    text-align: center;
    font-family: 'Times New Roman'
  }
  
  #viewRequirementTable {
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    border: 3px solid #ddd;
    width: 100%;
   
  }
  

#viewRequirementTable tr td:first-child::before {
    min-width: 1em;
    margin-right: 0.5em;
}
  
  #viewRequirementTable td, #table th {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  #viewRequirementTable tr:nth-child(even){background-color: #f2f2f2;}

  
  #viewRequirementTable tr:hover {background-color: #ddd;}
  
  #viewRequirementTable th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: center;
    background-color: #2A3F54;
    color: white;
  }
  .fa.fa-edit.fa-2x{
    color: #2A3F54;

  }
  .fa.fa-trash.fa-2x{
    color: #2A3F54;

  }

  .divHeight{
    height: 150px !important;
    width : 200px !important;
  }