   #title1 {
    text-align: center;
    /* padding-top: 50px; */
    font-family: 'Times New Roman'
  } 
  
  #viewAllAdminTable {
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    border: 3px solid #ddd;
    width: 100%;
    counter-reset: 0;
  }
  
  #viewAllAdminTable td, #table th {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  #viewAllAdminTable tr:nth-child(even){background-color: #f2f2f2;}
 
  #viewAllAdminTable tr {
    counter-increment: rowNumber;
}

#viewAllAdminTable tr td:first-child::before {
    content: counter(rowNumber);
    min-width: 1em;
    margin-right: 0.5em;
}
  
  #viewAllAdminTable tr:hover {background-color: #ddd;}
  
  #viewAllAdminTable th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: center;
    background-color: #2A3F54;
    color: white;
  }
  .fa.fa-eye:before {
    content: "\f06e";
    color: #2A3F54;
  }