.JobPostingTable{
    font-size: 15;
    margin-top: 2%;
}
.fa-file{
color: #000000;
}

.popupSelect{
    width: 58%;
}

#title {
    text-align: center;
    font-family: 'Times New Roman', Times, serif;
  }
  
  #JobPostingTable {
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    border: 3px solid #ddd;
    width: 100%;
  }
  
  #JobPostingTable td, #table th {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  #JobPostingTable tr:nth-child(even){background-color: #f2f2f2;}

  
  #JobPostingTable tr:hover {background-color: #ddd;}
  
  #JobPostingTable th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: center;
    background-color: #2A3F54;
    color: white;
  }
.fa-calendar{
  color: #000000;
}
.fa-file-excel-o {
    color: #000000; 
}

#JobPostingTable tr {
    counter-increment: rowNumber;
}
#JobPostingTable tr td:first-child::before {
    content: counter(rowNumber);
    min-width: 1em;
    margin-right: 0.5em;
}
.JobPostingTableInner{
    margin-top: 12%;
}