#viewLastweeksFeedbackTable {
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  border: 3px solid #ddd;
  width: 100%;
  counter-reset: 0;
 
}
#viewLastweeksFeedbackTable tr {
  counter-increment: rowNumber;
}

#viewLastweeksFeedbackTable tr td:first-child::before {
  content: counter(rowNumber);
  min-width: 1em;
  margin-right: 0.5em;
}

#viewLastweeksFeedbackTable td, #table th {
  border: 1px solid #ddd;
  padding: 8px;
}

#viewLastweeksFeedbackTable tr:nth-child(even){background-color: #f2f2f2;}


#viewLastweeksFeedbackTable tr:hover {background-color: #ddd;}

#viewLastweeksFeedbackTable th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: center;
  background-color: #2A3F54;
  color: white;
}
