#title {
    text-align: center;
    font-family: 'Times New Roman', Times, serif;
  }

  #title1 {
    text-align: center;
    padding-top: 50px;
    font-family: 'Times New Roman'
  }
  #titleHeading{
    text-align: center;
    font-family: 'Times New Roman'
  }
  
  #HireCandidateTable {
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    border: 3px solid #ddd;
    width: 100%;
    counter-reset: 0;
  }
  #HireCandidateTable tr {
    counter-increment: rowNumber;
}
#HireCandidateTable tr td:first-child::before {
  content: counter(rowNumber);
  min-width: 1em;
  margin-right: 0.5em;
}
  #HireCandidateTable td, #table th {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  
  #HireCandidateTable tr:nth-child(even){background-color: #f2f2f2;}

  
  #HireCandidateTable tr:hover {background-color: #ddd;}
  
  #HireCandidateTable th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: center;
    background-color: #2A3F54;
    color: white;
  }
