.unScheduledInterviewTable{
    font-size: 15;
    margin-top: 2%;
}

.roundSelect{
  float: right;
  width: 44%;
}
.fa-file{
color: #000000;
}

.popupSelect{
    width: 58%;
}

#title {
    text-align: center;
    font-family: 'Times New Roman', Times, serif;
  }
  
  #unScheduledInterviewTable {
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    border: 3px solid #dcdcdc;
    width: 100%;
  }
  
  #unScheduledInterviewTable td, #table th {
    border: 1px solid #dcdcdc;
    padding: 8px;
  }
  
  #unScheduledInterviewTable tr:nth-child(even){background-color: #f2f2f2;}

  
  #unScheduledInterviewTable tr:hover {background-color: #ddd;}
  
  #unScheduledInterviewTable th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: center;
    background-color: #2A3F54;
    color: white;
  }
.fa-calendar{
  color: #000000;
}
